import React, { Component } from 'react'
import { withStyles, Divider, List, ListItem, Avatar, Grid, Typography } from '@material-ui/core'
import { Link, withRouter } from 'react-router-dom'
import styles from '../styles/ClubSidebarContent'
import compose from 'recompose/compose'
import classNames from 'classnames';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import jwtDecode from 'jwt-decode';
import { Textfit } from 'react-textfit';
import { logOutUser } from "../../../redux/actions";
import PowerSettingsNew from "@material-ui/icons/PowerSettingsNew";

class ClubSideBarContent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            products: false,
            orders: false
        }
    }

    logOut = () => {
        localStorage.removeItem('user_token');
        const { logOutUser } = this.props;
        logOutUser();
        const { router } = this.context;
        router.history.push('/')
    }

    renderProduct() {
        const { classes, club, isClubProfile, registerUser } = this.props
        let club_id = null
        let user = '';
        if (localStorage.getItem('user_token')) {
            var decoded = jwtDecode(localStorage.getItem('user_token'));
            user = decoded.user.full_name

        }

        if (club)
            club_id = isClubProfile ? this.props.match.params.clubId : this.props.club._id;
        if (window.location.host.includes('javaniran.club') && window.location.pathname === '/') {
            club_id = "5ca89c77e1d47c25a0374f51"
        } else if (window.location.host.includes("tafriheman.net") && window.location.pathname === '/') {
            club_id = "5bdd57b4397fec163454204e"
        } else if (window.location.host.includes("localhost:3000") && window.location.pathname === '/') {
            club_id = "5bdd57b4397fec163454204e"
        }


        if (this.props.club && this.props.club._id !== '' && window.location.pathname === '/dashboard/product/list') {
            club_id = this.props.club._id
        }
        return (
            <div>
                {/*<ListItem*/}
                {/*classes={{root: classes.listItem}}*/}
                {/*>*/}
                {/*<Link to={`/clubs/${club_id}`} className={classes.link}>*/}
                {/*لیست محصولات*/}
                {/*</Link>*/}
                {/*</ListItem>*/}
                {/*<Divider/>*/}
                <ListItem classes={{ root: classes.listItem }} style={{ justifyContent: 'center' }}>
                    {
                        localStorage.getItem('user_token') &&
                        <div style={{ textAlign: 'center' }} >
                            <Avatar
                                src={require("./user.png")}
                                className={classNames(classes.avatar, classes.bigAvatar)}
                            />
                            <Link
                                className={classNames(classes.a, classes.link)}
                                to='/profile'
                            >
                                پروفایل کاربر
                    </Link>
                            <Textfit
                                className={classNames(classes.textFit)}
                                mode="multi"
                            >
                                {user}
                            </Textfit>
                            {
                                localStorage.getItem('user_token') &&
                                <div>
                                    <PowerSettingsNew
                                        className={classNames(classes.PowerSettingsNew)}
                                    >
                                    </PowerSettingsNew >
                                    <br />
                                    <listItem
                                        onClick={this.logOut.bind(this)}
                                        classes={{ root: classes.listItem }} 
                                        style={{ color: "#667f7f",  cursor: 'pointer' }}>خروج</listItem>
                                </div>
                            }
                        </div>
                    }
                </ListItem>
                <Divider />
                <ListItem
                    classes={{ root: classes.listItem }}
                >
                    <Link to={`/`} className={classes.link}>
                        صفحه اصلی
                    </Link>
                </ListItem>
                <Divider />
                <ListItem
                    classes={{ root: classes.listItem }}
                >
                    <a href={`https://tafriheman.net/help/what-is-tafriheman/`} target='_blank' className={classes.link}>
                        راهنما
                    </a>
                </ListItem>
                <Divider />

                {
                    localStorage.getItem('user_token') &&
                    <ListItem
                        classes={{ root: classes.listItem }}
                    >
                        <Link to={`/orders`} className={classes.link}>
                            لیست سفارشات
                    </Link>
                    </ListItem>
                }
                <Divider />
                {
                    localStorage.getItem('user_token') &&
                    <ListItem
                        classes={{ root: classes.listItem }}
                    >
                        <Link to={`/messages`} className={classes.link}>
                            لیست پیام ها
                    </Link>
                    </ListItem>
                }
            </div >
        )
    }

    renderOrders() {
        const { classes } = this.props
        if (this.state.orders)
            return (
                <div>
                    <ListItem>
                        <List disablePadding component="ul">
                            <ListItem classes={{ root: classes.listItem }}>
                                <Link to="/dashboard/labels" className={classes.link}>
                                    برچسب گذاری
                                </Link>
                            </ListItem>
                            <ListItem classes={{ root: classes.listItem }}>
                                <Link to="/dashboard/checkLists" className={classes.link}>
                                    چک لیست
                                </Link>
                            </ListItem>
                            <ListItem classes={{ root: classes.listItem }}>
                                <Link to="/dashboard/order" className={classes.link}>
                                    سفارشات
                                </Link>
                            </ListItem>
                        </List>
                    </ListItem>
                    <Divider />
                </div>
            )
    }


    render() {
        const { classes } = this.props;
        // var decoded = jwtDecode(localStorage.getItem('TAFRIHEMAN_CLUB_UESR@KEY'));
        return (
            <div>
                <Grid container justify="center" alignItems="center" direction='column'>
                    {/*<Avatar className={classes.bigAvatar}>C</Avatar>*/}
                    <Typography></Typography>
                </Grid>
                <List component="ul" disablePadding>


                    <Divider />
                    {this.renderProduct()}

                    {/*<ListItem
                     button
                     classes={{root: classes.listItem}}
                     >
                     قرعه کشی‌ها
                     </ListItem>

                     <Divider/>

                     {this.renderOrders()}

                     <ListItem
                     button
                     classes={{root: classes.listItem}}
                     >
                     تخفیف‌ها
                     </ListItem>

                     <Divider/>

                     <ListItem
                     button
                     classes={{root: classes.listItem}}
                     >
                     رویدادها و دورهمی‌ها
                     </ListItem>

                     <Divider/>

                     <ListItem
                     button
                     classes={{root: classes.listItem}}
                     >
                     کمپین‌ها و هدایا
                     </ListItem>

                     <Divider/>

                     <ListItem
                     button
                     classes={{root: classes.listItem}}
                     >
                     سفارشات
                     </ListItem>
                     <Divider/>
                     {this.renderOrders()}

                     <ListItem
                     component="a"
                     href="https://tafriheman.net/help"
                     style={{textAlign: 'right', color: 'black'}}
                     >
                     درباره ما
                     </ListItem>
                     <Divider/>*/}
                </List>
            </div>
        )
    }
}

ClubSideBarContent.contextTypes = {
    router: PropTypes.object
};
const mapStateToProps = (state, { app }) => {
    const registerUser = state.app.registerUser;
    return { ...app, registerUser }
}

export default withRouter(
    compose(
        withStyles(styles),
        connect(mapStateToProps, {
            logOutUser
        }),
    )(ClubSideBarContent))
